<!-- eslint-disable vuejs-accessibility/mouse-events-have-key-events -->
<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <div class="notepad content-box">
    <div class="top rounded-0">
      <h3 class="text-uppercase text-center">shopping list</h3>
    </div>
    <div class="paper d-flex flex-column justify-content-between">
      <div>
        <div class="row text-nowrap">
          <span class="col-4">Name</span>
          <span class="col-2">Price</span>
          <span class="col-2">Qty</span>
          <span class="col-2">Sub</span>
          <span class="col-2">Edit</span>
        </div>
        <template v-for="(item, key) in carts.carts" :key="key">
          <div class="row">
            <span class="col-4">{{ item.product.title }}</span>
            <span class="col-2 text-nowrap">${{ item.product.price }}</span>
            <span class="col-2 text-nowrap">X{{ item.qty }}</span>
            <span class="col-2 text-nowrap"
              >${{ this.$toCurrency(item.qty * item.product.price) }}</span
            >
            <span class="col-2 text-center"
              ><a href="#" @click.prevent="setData(item)"><i class="bi bi-pencil"></i></a
            ></span>
          </div>
        </template>
      </div>
      <div class="modal-footer pb-0 d-flex flex-column align-items-end">
        <div class="mb-3">Total：${{ this.$toCurrency(1000) }}</div>
        <div class="d-flex justify-content-between w-100">
          <router-link
            to="/products"
            @mouseenter="cicleStatus.shopping = true"
            @mouseleave="cicleStatus.shopping = false"
            class="font-monospace fw-bold titleText position-relative"
          >
            <span ref="shop"></span><i class="bi bi-pencil-fill"></i>
            <Cicleone :class="{ 'd-none': !cicleStatus.shopping }" />
          </router-link>
          <router-link
            to="/checkout"
            @mouseenter="cicleStatus.checkout = true"
            @mouseleave="cicleStatus.checkout = false"
            class="font-monospace fw-bold titleText position-relative"
            ><i class="bi bi-caret-right-fill" ref="checkout"></i>Checkout
            <Cicletwo :class="{ 'd-none': !cicleStatus.checkout }" />
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
@import '@/assets/stylesheets/helpers/_label.scss';
.circle-1 {
  width: 9rem;
}
.circle-2 {
  width: 10rem;
}
</style>

<script>
import Cicleone from '@/components/models/circle/CircleOne.vue';
import Cicletwo from '@/components/models/circle/CircleTwo.vue';
import { gsap } from 'gsap';

export default {
  data() {
    return {
      carts: [],
      cicleStatus: {
        shopping: false,
        checkout: false,
      },
      cartModal: '',
      tempData: '',
    };
  },
  components: {
    Cicleone,
    Cicletwo,
  },
  methods: {
    getCarts() {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http.get(api).then((res) => {
        this.carts = res.data.data;
      });
    },
  },
  mounted() {
    this.getCarts();
    const timeline = gsap.timeline();
    timeline
      .to(this.$refs.shop, {
        duration: 3,
        text: { value: 'Shopping', delimiter: '' },
        repeat: -1,
      })
      .to(
        this.$refs.checkout,
        {
          opacity: 0,
          duration: 1.5,
          repeat: -1,
        },
        '<',
      );
  },
};
</script>
