<template>
  <section class="d-flex flex-column align-items-center">
    <div class="mb-3">
      <wired-calendar :selected="`${date.dateMonth} ${date.today}, ${date.dateYears}`">
      </wired-calendar>
    </div>
    <h4>{{date.week}} {{ date.today }} {{ date.dateMonth }}</h4>
    <h5>8:00AM-6:00PM</h5>
  </section>
</template>

<script>
export default {
  data() {
    return {
      week: ['SUN', 'MON', 'TUES', 'WED', 'THURS', 'FRI', 'SAT'],
      date: {
        dateYears: '',
        dateMonth: '',
        today: '',
        week: '',
      },
    };
  },
  mounted() {
    const newDate = new Date();
    this.date.dateYears = newDate.getFullYear();
    this.date.dateMonth = newDate.toLocaleString('en-US', { month: 'short' });
    this.date.today = newDate.getDate();
    this.week.forEach((item, i) => {
      if (i === newDate.getDay()) {
        this.date.week = item;
      }
    });
  },
};
</script>
